.about-container {
  padding: 30px 15px;
  background: #ded5ba;
  color: #222222;
}

ul a {
  color: #466aa8;
  -webkit-transition: .3s ease;
  -o-transition: .3s ease;
  transition: .3s ease;
  font-size: 1.3em;
}

ul a:hover {
  text-decoration: none;
}

ul {
  list-style-type: circle;
}


