.footer-container {
  background: #222222;
  color: white;
  padding: 30px 15px;
  height: 200px;
  text-align: center;
  /*position: fixed;*/
  /*bottom: 0;*/
  width: 100%;
  z-index: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.footer-container a {
  text-decoration: none;
  -webkit-transition: .3s ease;
  -o-transition: .3s ease;
  transition: .3s ease;
  color: white;
}

.footer-container a:hover {
  text-decoration: none;
  color: #666666;
}


